@import "../vars.scss";

@mixin responsive {
  @media screen and (max-width:545px) {
    width: 90%;
  }
}

.feedback{
  align-items: baseline;

  .liking{
    width:35%;

    @include responsive();
  }

  .icon{
    fill: $color;

    &:hover{
      transform: scale(1.1);
      filter: hue-rotate(45deg);
    }

  }
  
  form{
    box-shadow: none;
    width: 55%;
    border: solid 1px $color;
    
    input{
      text-align: center;
    }
    
    @include responsive();
  }
}