@import '../vars.scss';

.welcome-card{
	background: radial-gradient($background, $background);
	height:  266px;
	$transition: .5s;
	cursor: pointer;
	transition: $transition;
	button{
			transition: $transition;
	}
	&:hover,&:focus{
		background: radial-gradient(rgba($primary, .6), $background);
		color: $color;

		button, .button{
				background: $background;
		}
	}
}

.slider-cards{
    margin: 40px 0;
    display: grid;
    grid-template-columns: repeat( 3 ,50vw);
    justify-items: center;

    @media screen and (max-width: 690px) {
        grid-template-columns: repeat( 3 ,100vw);
    }
}

.slide-container{
    width: 100%;
    position: relative;
    overflow-x: hidden;
}