@import './vars.scss';
@import './fonts.scss';
@import './utils.scss';

*{
    box-sizing: border-box;
    user-select: none;
    transition:  .3s;
    scroll-behavior: smooth;
}

body{
    margin: 0;
    padding: 0;
    background-color: $background;
    color: $color;
    font-family: 'jost' ,sans-serif;
    width: 100%;
    height: 95vh;
    // overflow: hidden;
}

h1, h2,h3, h4, h5{
    font-size: 30px;
    margin: $margin;
    padding: 0;
    text-transform: capitalize;
    font-weight: 200;
}

button, .button, select{
    @extend .radius;
    @extend .shaded;
    border: none;
    background: $primary;
    color: $color;
    font-size: 1.1rem;
    padding: 10px 20px;
    cursor: pointer;

    // &:active{
    //     transform: scale(.8);
    // }
}

a{
    text-decoration: none;
}

select{
    background: initial;
}

input, textarea{
    background: $background;
    color: $color;
    border: none;
    outline: none;
    padding: 10px 30px;
    border-radius: 3px;

    &:focus{
        background: rgba(grey, .4);
    }
}

input[type=text],textarea{
    @extend .card;
}

input[type=radio],input[type=checkbox] {
    cursor: pointer;
    margin: 10px 20px;
    width: 25px;height: 25px;
    position: relative;
    
    &::before, &::after{
        box-sizing: border-box;
        content: "";
        position: absolute;
        background: $background;
        border-radius: 50%;
    }
    &::before{
        width: 100%;
        height: 100%;
        border: solid 2px $primary;
    }

    &::after{
        top: 0; left: 0;
        right: 0;bottom: 0;
        margin: auto;
        width: 60%;height: 60%;
        background: $primary;
        opacity: 0;
        transform: scale(.8);
        transition: .4s;
    }

    &:checked::after{
        opacity: 1;transform: scale(1);
    }

}

// les classes specialement concus pour le design
.titre{
    max-height: 50vh;
    min-height: 103px;
    height: fit-content;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;

    position: relative;
    h1{
        @extend %flex;
        font-size: 100px;
        font-weight: 300;
        left: 0;top: 0;
        position: absolute;
        width: 100%;
        height: #{calc(100%)};
    }
    
    .affiche{
        width: 100%;
    }
}

.loader{
    position: fixed;
    top: 0; left: 0;

    background: transparent;
    backdrop-filter: blur(10px);
    width: 100%;
    height: #{calc(100%)};

    $duration: .2s;
    .dot{
        display: inline-block;
        animation: bounce calc($duration * 5) infinite linear;
    }
    .dot-a{
        animation: fade calc($duration * 3) infinite linear;
    }

    @for $i from 1 through 3 {
        .dot, .dot-a{&:nth-child(#{$i}){
            animation-delay: calc($duration * $i);
        }}
    }
}

// classes multi usage
.flex{
    @extend %flex;

    &.row{
        flex-direction: row;
        justify-content: space-around;
        flex-wrap: wrap;

        &.left{
            justify-content: flex-start;
        }

        &.right{
            justify-content: flex-end;
        }
    }

    &.stretch{
        align-items: stretch;
    }

    &.left{
        align-items: flex-start;
    }

    &.right{
        align-items: flex-end;
    }

    &.center{
        justify-content: center;
    }

    &.between{
        justify-content: space-between;
    }
    
    &.around{
        justify-content: space-around;
    }

    &.nowrap{
        flex-wrap: nowrap;
    }
}

.ripple, button{
    cursor: pointer;
    position: relative;
    overflow: hidden;
    .rippler{
        position: absolute;
        top: 0;left: 0;
        width: 100%;height: #{calc(100%)};
        background: $color;
        opacity: .4;
        transition: opacity .1;
    }
}

.letters{
    @extend .flex;
    flex-direction: row !important;

    span.letter{
        overflow: hidden;line-height: 1;
        span{
            text-transform: lowercase;
            display: block;
            width: 100%;
            transition: .4s;
        }
    }
    
    &.animated{
        span{
            transform: translateY(100%);
            // une annimation pour le test avant javascript
            animation: appear 0.3s forwards cubic-bezier(0.67, -0.01, 0.49, 1.01);
        }
        
    }
}

.marge-left{
    margin-left: calc($nav-bar + 30px);
}

.pad-left{
    padding-left: calc($nav-bar + 30px);
}

.bg{
    background: $background;
}

.border{
    border: solid 1px $primary;
}

/** le scrollBar **/
*::-webkit-scrollbar{
    @extend .shaded;
    $size: 10px;
    background-color: $background;
    width: $size;height: $size;
    border-radius: calc($size / 2);

    &-thumb{
        border: solid 2px $background;
        background: $primary;
        border-radius: calc($size / 2);
    }
}

/*****************/
*:focus{
    outline: none;
}

@keyframes appear {
    from{
        transform: translateY(100%);
    }
    to{
        transform: translateY(0);
    }
}

@keyframes bounce {
    0%{
       transform: translateY(0); 
    }

    30%{
        transform: translateY(-60%); 
    }

    40%{
        transform: translateY(-50%); 
    }

    65%{
        transform: translateY(-5%);
    }

    100%{
        transform: translateY(0);
    }
}

@keyframes fade {
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0;
    }
    90%{
        opacity: 1;
    }
    100%{
        opacity: 1;
    }
}