@import "../vars.scss";

.popover{
    position: relative;
    overflow-x: visible;

    .popup{
        $tr : 20px;
        position: absolute;
        right: 0;top: 0;
        transform: translateX(#{calc(100% + $tr)}) scale(1);
        height: #{calc(100%)};
        padding: $padding;
        transform-origin: 0 0;
        margin-left: $margin;
        animation: scl 1s forwards ease-in;

        // &.hide{
        //     transform: scale(0);
        // }
    }
}

