@import "../vars.scss";
$controls-heiht: 60px;

.video-page{
  .sticky{
    position: sticky;
    top: 0;
    left: 0;

    &.fixed{
      background: $primary;
      padding: $padding;
      box-shadow: $shadow;
      z-index: 4;
    }
  }

  .video-loader{
    --size: 100px;
    position: absolute;
    top: auto;left: auto;
  }

}

.video-container{
  width:fit-content;
  border-radius: $radius;
  margin: $margin;
  overflow: hidden;
  background: $primary;

  position: relative;
  .controls{
    position: absolute;
    width: 100%;
    height: $controls-heiht;
    background: rgba($color: $background, $alpha: .8);
    bottom: 0;left: 0;
    padding-top: $margin;

    .trames{
      $cursor: 10px;
      $h:2px;

      position: relative;
      align-items: baseline;
      height: $cursor;
      width: 100%;
      margin: $margin 0;
      cursor: pointer;

      .abs{
        background: $primary;
      }
      
      
      &:hover .readed{
        transform: scaleY(1.6);
      }
      .readed{
        border-radius: $h;
        width: 100%;
        height: $h;
      }
      
      .cursor{
        
        position: absolute;
        width: $cursor;height: $cursor;
        border-radius: 50%;
        left: 0;
        
        &:hover{
          transform: scale(1.6);
        }
      }
    }
  }

  video{
    max-width: 100%;
    height: 60vh;
  }

  .skeleton{
    $size: 35px;

    padding: 0 10px;

    %sized{
      width: $size;height: $size;
    }

    .circle{
      @extend %sized;
      border-radius: 50%;
      padding: $margin;
    }

    .lines{
      padding: 4px nth($list: $padding, $n: 1);
      height: $size;
      align-items: flex-start;
    }

    .line{
      margin-left: $margin;
      height: 4px;
      border-radius: 3px;
      width: calc(2 * $size);
      &:nth-child(1){
        width: calc(3 * $size);
      }
    }

    .square{
      @extend %sized;
      border-radius: $radius;
      margin:$margin;
      &.fake{
        display: none;
      }
    }
    .hidder{
      display: none;
    }

    
    @media screen and (max-width: 420px) {
      .hidder{
        display: flex;
      }

      .square.fake{
        display: flex;
      }

      .squares{
        flex-direction: column;
        position: absolute;
        right: 0;
        bottom: 0;
        transition: .3s;
        transform-origin: bottom right;
        transform: scale(1);

        &.hidden{
          transform: scale(0);
        }
      }

    }
  }

  .big-centered-button{
    --size: 80px;
    position: absolute;
    top: auto;left: auto;
    padding: $margin;

    opacity: 0;
    //   &:hover{
    //   opacity: 1;
    // }
  }

  .hidable{
    opacity: 0;
  }

  &:hover{
    .hidable{
      opacity: 1;
    }
  }
}

h3.expand{
  padding-left: 10px;
}

.caroussel{
  width: 90vw;
  overflow: hidden;

  position: relative;
  .icon.circle{
    fill: $color;
    padding: 10px;
    --size:40px;


    z-index: 4;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    &.left{
      left: 0;
    }

    &.right{
      right: 0;
    }

    &.disabled{
      filter: brightness(40%);
    }
  }

}

div.slide-index{
  --size: 2rem;
  border: solid 2px $color;
  margin: $margin;
  cursor: pointer;

  &.disabled{
    opacity: .3;
  }
}

// style de composant
.caroussel-item{
  $square: 40px;
  overflow-x: visible;

  cursor: pointer;
  margin:$square ;

  width: 25vh;
  height: 40vh;

  position: relative;
  .abs{
    position: absolute;
  }

  .square{
    width: $square;
    height: $square;
    z-index: -1;

    &.top{
      top: 0;
      left: 0;
    }

    &.bottom{
      bottom: 0;
      right: 0;
    }
  }

  .mask{
    z-index: 2;
    height: #{calc(100%)};
    width: 100%;
    background-color: rgba($color: $background, $alpha: .6);
    opacity: 0;
  }

  &:hover{
    .top{
      transform: translate(-40%, -40%);
    }
    .bottom{
      transform: translate(40%, 40%);
    }

    .mask{
      opacity: 1;
    }
  }

  .img-masker{
    width: 100%;
    overflow: hidden;
    img{
      min-width: 100%;
    }
  }

  img,.img-masker{
    height: #{calc(100%)};
  };
}