@import "../vars.scss";

$left-bottom: 0 200%;
$right-bottom: 200%  200%;

.spining-loader{
  --size:40px;
  width: var(--size);height: var(--size);
  border-radius: 50%;
  clip-path: polygon($left-bottom, $right-bottom, 50% 50%);
  animation: spin 1.5s ease-in-out infinite;

  // les classes de couleur
  @for $index from 1 through length($colors) {
    $bg: nth($colors, $index);
    $class: nth($classes, $index);

    &.#{$class}{
        background: transparent;
        border: solid 5px $bg ;
    }
  }
}

@keyframes spin {
  from{
    transform:rotate(0);
  }
  to{
    transform:rotate(360deg);
  }
}