@import "../vars.scss";

$nav-width: 60px;

#left-nav{
  margin: 0 $margin 0 0 ;
  height: #{calc(100%)};

}

.link{
  padding: 5px;
  margin: 20px 0;
  border-radius: $radius;

  &.active{
    box-shadow:$shadow;
  }
}

.admin{
  display: grid;
  grid-template-columns: $nav-width 1fr;
  // height: #{calc(100%)};
  height: 90vh;
  overflow-y: hidden;

  .main-content{
    overflow-y: scroll;
  }

  .sticky-title{
    position: sticky;
    z-index: 2;
    top: 0;
  }

}

.create{

  .input-container{
    padding: $padding;
    
    label{
      cursor: pointer;
    }
  }

  input[type=text], textarea{
    background: $color;
    color: $background;
    font-size: 1.1rem;
  }

  .source-container{
    height: 40vh;
    cursor: pointer;

    .icon{
      height: 40px;
      width:  40px;
      margin: 10px;
    }
  }

  .categories{
    padding: $padding;
    border-radius: $radius;
    width: 50%;
    min-width: 200px;

    .input-container{
      flex-wrap: nowrap;
      align-items: center;

    }
  }
}

.source{
  display: inline;

  .separator{
    background: rgba($color, .4);
    border-radius: $margin;
    padding:  $margin;
    margin: $margin;
  }
}

.animated-route{
  animation: show .6s forwards ease-out;
}

.list-item{
  cursor: pointer;
  .title{
    width: 35%;
    max-width: 245px;
    min-width: 150px;
    word-wrap: unset;
    max-height: 2rem;
    overflow: hidden;
  }

  &.focused{
    padding: 30px 0;
  }
}

@keyframes show {
  0%{
    transform: translateY(30px);
    opacity: 0;
  }
  100%{
    transform: translateY(0);
    opacity: 1;
  }
}