@import "../vars.scss";

#nav{
    position: fixed;
    top: 0;left: 0;
    z-index: 5;
    width: fit-content;
    overflow-x: visible;
    height: #{calc(100%)};

    .nav-container{
        transform-origin: bottom;
        transform: scaleY(1);
        padding: 5px;
        min-height: #{calc(80%)};
        background: $background;
    }

    button, .button{
        width: $nav-bar;
        margin: 5px;
        background: $background;
        color: $primary;
        padding: 10px;
    }

    .unroller, .roller{padding: 3px;
        display: none;
    }
}

@media screen and (max-width:345px) {
    
    #nav.rolled{
        top: initial;
        bottom: 10px;;
        height: fit-content;
        border-radius: 50%;
        
        .unroller{
            display: flex;
        }

        .roller{
            display: none !important;
        }

        .nav-container{
            height: 0;
            transform: scaleY(0);
        }
    }

    .roller{
        display: flex !important;
    }

    .marge-left{
        margin-left: 0 !important;
    }

    .pad-left{
        padding-left: 0 !important;
    }
}
