@import "../vars.scss";

.login-route{
  .card{
    padding: 0;
    width: 950px;
    max-width: 95%;
    min-height: 70vh;
    overflow: hidden;
  }

  .half-section{
    width: 50%;
    min-width: 300px;
    padding: $padding;
  }

  .steps{
    padding: $margin;
    .step{
      --size: 40px;
      z-index: 1;
    }

    position: relative;
    .progress{
      width: 50%;
      height: 4px;border-radius: 2px;

      z-index: 0;
      position: absolute;
      top: auto;left: 0;
    }
  }

  h3{
    font-size: 1.2rem;
    padding: $padding;
  }

  form{
    input{
      border-radius: $radius;
      border: solid 1px $background;
      margin: $margin;
    }
  }

  .form-container{
    background: url(../../assets/login-background.jpg) center/cover no-repeat;

    $duration: .4s;
    $timing-function: ease-in-out;
    .half-section.primary-text:nth-child(2){
      animation: slide $duration forwards $timing-function;
    }
    .half-section.primary-text:nth-child(1){
      animation: slideReverse $duration forwards $timing-function;
      z-index: 2;
    }

    .half-section.transparent{
      opacity: 0;
      transition: $duration;
      opacity: 1;
    }
  }
}


@keyframes slide {
  from{
    transform: translateX(-100%);
  }
  to{
    transform: translateX(0%);
  }
}
@keyframes slideReverse {
  from{
    transform: translateX(100%);
  }
  to{
    transform: translateX(0%);
  }
}