@import "../vars.scss";

.update-page{
  margin: 0;

  .update-source{
    height: 40vh;
  }

  .update-poster{
    position: relative;
    .poster{
      --size: 320px;
    }

    .masker{
      position: absolute;
      bottom: 0;left: 0;
      opacity: 0;
      transition: .5s;
    }

    &:hover .masker{
      opacity: 1;
    }
  }
}