@import "../vars.scss";

// utilisee par le composant poster

$h: 200px;
$w: calc($h * 0.6);
article.poster{
  --size:#{$h};
  cursor: pointer;
  height: #{calc(var(--size))};width: #{calc(var(--size) * 0.6)} ;
  background: $primary;
  padding: 0;margin: 0 10px;

  overflow: hidden;

  img{
      min-height: 100%;
      min-width: 100%;
      transition: .5s;

      &:hover{
      transform: scale(1.4);
      }
  }
}

$space: 30px;

// utilisee par le composant poster
.carte{
  background: center/cover no-repeat;
  &:hover{
    background: left bottom/cover no-repeat;
  }
  
  .mask{
    padding: $padding;
    background: rgba($color: $background, $alpha: .7);
  }
  .descri{
    box-sizing: border-box;
    margin-left: $space;
    max-width: #{calc(92% - $w )};
  }
  .btns button{
    margin: 0 $space;
  }
}