@import "../vars.scss";

.affiche{
  .mask{
    $opac: rgba($color: $background, $alpha: .6);

    position: absolute;
    top: 0;left: 0;

    height: #{calc(100%)};width: 100%;
    background: linear-gradient($opac, $background);
  }
}

.article-container{
  flex-direction: row;
  overflow: hidden;
}