@import "../vars.scss";

$icon-size: 25px;
$m: 5px;

.icon{
  fill: $primary;
  width: $icon-size;
  height: $icon-size;
  margin: $m;
  cursor: pointer;

  &.transit{
    animation: icon .5s ease-in forwards;
  }
}

button .icon{
  fill: $color;
}

@keyframes icon {
  0%{
    opacity: 0;
    transform: rotate(45deg);
  }
  100%{
    opacity: 1;
    transform: rotate(0);
  }
}