@import './vars.scss';

// les classes utiles 
%flex{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.radius{
    border-radius: $radius;
}
.shaded{
    box-shadow: $shadow;
    margin: $margin;
    padding: $padding;
}
.card{
    @extend .radius;
    @extend .shaded;
    max-width: 100%;
    width: 300px;
}

.main{
    overflow-x: hidden;
    min-height: 98vh;
}

.expand{
    width: 100%;
}

button.circle{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    --size:40px;

    *{
        margin: 0;
    }
}

.circle{
    --size:20px;
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
}

.date{
    color: rgba($color, .6);
}

.badge{
    border-radius: $margin;
    padding: 1px $margin;
    margin: $margin;
}

.float{
    position: fixed;
    top: 0;left: 0;

    &.right{
        right: 0;left: unset;
    }
    &.bottom{
        bottom: 0;top: unset;
    }
}

.popup-container{
    position: fixed;
    top: 0;left: 0;
    width: 100%;
    height: #{calc(100%)};
    backdrop-filter: blur(20px);
}

.pointer{
    cursor: pointer;
}

// les classes de couleur
@for $index from 1 through length($colors) {
    $bg: nth($colors, $index);
    $class: nth($classes, $index);

    .#{$class}{
        &, &.button{
            background-color: $bg;
            @if lightness($bg) > 60% {
                color: $background;
            }
            .icon{
                fill: $color;
            }
        }
    }
}
